<template>
  <div class="box">
      <div class="border">
          <div class="line line-l"></div>
          <div class="line line-t"></div>
          <div class="line line-r"></div>
          <div class="line line-b"></div>
          <div class="lb">
              <div class="lb1"></div>
              <div class="lb2"></div>
              <div class="lb3"></div>
          </div>
          <div class="rt">
              <div class="rt1"></div>
              <div class="rt2"></div>
              <div class="rt3"></div>
          </div>
          <div>content</div>
          <div class="spot"></div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.box {
    margin: 50px;
}
.border {
    position: relative;
    width: 200px;
    height: 90px;
    line-height: 90px;
}
.line {
    background: #666;
    position: absolute;
}
.line-l {
    left: 0;
    width: 1px;
    height: 70px;
}
.line-t {
    width: 170px;
    height: 1px;
    top: 0;
    left: 0;
}
.line-r {
    right: 0;
    width: 1px;
    height: 70px;
    bottom: 0;
}
.line-b {
    width: 170px;
    height: 1px;
    bottom: 0;
    right: 0;
}
.lb {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 30px;
    height: 20px;
}
.lb1 {
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 9px;
    background: blue;
}
.lb2 {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 19px;
    height: 1px;
    transform: translate(-2px, -5px) rotateZ(35deg);
    background: blue;
}
.lb3 {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 15px;
    height: 1px;
    background: blue;
}
.rt {
    position: absolute;
    right: 0;
    top: 0;
    width: 30px;
    height: 20px;
}
.rt1 {
    position: absolute;
    left: 0;
    top: 0;
    width: 15px;
    height: 1px;
    background: blue;
}
.rt2 {
    position: absolute;
    right: 0;
    bottom: top;
    width: 19px;
    height: 1px;
    transform: translate(2px, 5px) rotateZ(35deg);
    background: blue;
}
.rt3 {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 1px;
    height: 9px;
    background: blue;
}
.spot {
    width: 5px;
    height: 5px;
    border-radius: 50%;
    position: absolute;
    left: 20px;
    bottom: 0;
    background: red;
    animation-name: run;
    animation-duration: 3s;
    animation-delay: 2s;
    animation-iteration-count: infinite;
}
@keyframes run {
    0%   {left:20px;}
    55%  {left:200px;bottom: 0;}
    100%  {bottom:75px; left: 200px;}
}

</style>



